const app = getApp()

export default {
	data() {
		return {
			id: 0,
			url: "/static/cardback0.jpg",
			activity: {
				id: 0,
				name: '活动名',
				intro: '活动介绍语',
				tickets: 80,
				location: '活动地点',
				start: '2019年xx月xx日',
				end: '',
				orgnizationid: 100,
				orgnizationname: 'xx学生会',
				type: 1,
				state: 200
			},
			current: 0,
			tabs: [
				"介绍", "动态"
			]
		};
	},
	onLoad(option) {
		console.log(option)
		uni.request({
		    url: 'http://154.8.167.168:8080', //仅为示例，并非真实接口地址。
		    data: {
		        id: option.id
		    },
		    header: {
		        'content-type': 'application/json' //自定义请求头信息
		    },
		    success: (res) => {
		        console.log(res.data);
		    }
		});
		return 0
	},
	methods: {
		tabSelect(e) {
			this.current = e.currentTarget.dataset.id;
		},
		navChange(index) {
			this.current = index;
		},
		swiperChange(e) {
			this.current = e.detail.current;
		},
		reserve() {
			uni.request({
			    url: 'http://154.8.167.168:8080', //仅为示例，并非真实接口地址。
			    data: {
			        activityid: this.id,
					userid: app.globalData.userInfo
			    },
			    header: {
			        'content-type': 'application/json' //自定义请求头信息
			    },
			    success: (res) => {
			        console.log(res.data);
			    }
			});
		},
		share() {
			
		},
		like() {
			
		}
	}
}
